<template>
  <div class="hd">
    <h1 class="page_title">{{voteInfo.title}}</h1>
  </div>
  <div class="bd">
    <cells type="radio">
      <radio-cell
        v-for="(key,option) in voteInfo.options"
        :id="$index | setId 'vote-item-'"
        name="vote-item"
        :value="key"
        :label="$index | toCharCode option.title"
        :picked.sync="radioPicked">
      </radio-cell>
    </cells>
    <button-area>
      <weui-button type="primary" @click="doVote">提交投票</weui-button>
    </button-area>
    <toast type="loading" v-show="loadingToastShow">投票处理中...</toast>
  </div>
</template>
<script>
  import store from '../store';
  import {Cells,Cell,CellsTitle,RadioCell,ButtonArea,Button as WeuiButton,Toast} from 'vue-weui';
  export default {
    name:"Detail",
    components:{Cells,Cell,CellsTitle,RadioCell,ButtonArea, WeuiButton,Toast},
    data(){
      return {
        loadingToastShow:false,
        radioPicked:'',
        voteInfo:{
          title:'',
          options:{}
        }
      }
    },
    route:{
      data:function(){
        document.title = "投票";
        return store.fetchVote().then(function(vote){
          console.log(vote);
          return {
            voteInfo:vote
          }
        })
      }
    },
    created () {
      store.on('updated', this.update)
    },

    destroyed () {
      store.removeListener('updated', this.update)
    },
    methods: {
      doVote:function(){
        this.loadingToastShow = true;
        if(!!this.radioPicked){
          store.setVote(this.radioPicked);
        }else{

        }
      },
      update (){
        var that = this;
        setTimeout(function(){
          that.$router.go('result')
        },800)
      }
    }
  }

</script>


