<template>
  <div class="question">{{ voteInfo.title }}</div>
  <div class="vote-warp2">
    <div class="column"  v-for="option in voteInfo.options" track-by="$index">
      <div class="option">{{* $index | toCharCode option.title }}</div>
      <div class="progress">
        <div class="progress-bd" :style="{ height: option.percentage }"> </div>
      </div>
    </div>
  </div>
  <div class="vote-total">共有{{ voteInfo.total }}人进行了投票</div>
</template>
<script>
  import _ from 'underscore'
  import store from '../store'
  export default{
    data(){
      return{
        voteInfo:{}
      }
    },
    route:{
      data:function(){
        document.title = "投票结果展示";
        return store.fetchVote().then(function(vote){
          var voteOptions = _.values(vote.options);
          var maxValue    = _.max(voteOptions,function(option){return option.value}).value;
          _.mapObject(voteOptions,function(val,key){
            return voteOptions[key]['percentage'] = Math.round(val.value/maxValue*100)+'%';
          });
          return {
            voteInfo:vote
          }
        })
      }
    },
    created () {
      store.on('updated', this.update)
    },
    destroyed () {
      store.removeListener('updated', this.update)
    },
    methods : {
      update(){
        var that = this;
        store.fetchVote().then(function(vote){
          var voteOptions = _.values(vote.options);
          var maxValue    = _.max(voteOptions,function(option){return option.value}).value;
          _.mapObject(voteOptions,function(val,key){
            voteOptions[key]['percentage'] = Math.round(val.value/maxValue*100)+'%';
          });
          that.voteInfo = vote;
        })
      }
    }
  }
</script>
<style>
  *{margin: 0; padding: 0;box-sizing: border-box;}
  html,body{height: 100%; font-family:"微软雅黑";}
  .page{height: 100%;}
  ul,li{list-style: none;}
  .question{
    height: 80px;
    line-height:80px;
    font-size:30px;
    text-align: center;
  }
  .vote-warp2{
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -webkit-justify-content: space-between;
    -ms-flex-pack: justify;
    justify-content: space-between;

    position: absolute;
    width: 100%;
    top:80px;
    bottom: 40px;
    background: #f0f0f0;

  }
  .column{
    flex-grow:1;
    background: #666;
    position: relative;
    -webkit-transition: all 1s;
    -moz-transition: all 1s;
    -ms-transition: all 1s;
    -o-transition: all 1s;
    transition: all 1s;
  }
  .vote-total{
    position: absolute;
    padding-right: 20px;
    bottom: 0;
    width: 100%;
    height:40px;
    line-height: 40px;
    text-align: right;
    font-size:16px;
  }
  .option{
    height:40px;
    line-height: 40px;
    position: absolute;
    bottom: 0;
    width: 100%;
    padding:0 20px;
    text-align: center;
    color: #fff;
  }
  .progress{
    position: absolute;
    top: 0;
    bottom: 40px;
    width: 100%;
    background: #ccc;
  }

  .progress-bd{
    margin: 0 auto;
    width: 80%;
    background: #333;
    position: absolute;
    height: 0;
    bottom: 0;
    right: 0;
    left: 0;
    -webkit-transition: all .3s ease;
    -moz-transition: all .3s ease;
    -ms-transition: all .3s ease;
    -o-transition: all .3s ease;
    transition: all .3s ease;
  }
</style>


