import Wilddog from 'wilddog'
import _ from 'underscore'
import { EventEmitter } from 'events'
import { Promise } from 'es6-promise'

const ref = new Wilddog("https://vote160314.wilddogio.com");
//const itemsCache = Object.create(null)
const userRef = ref.child('users');
const voteRef = ref.child('votes/-KDNS7AYzMOXNegdkRP2');
const store = new EventEmitter();
export default store;

let voteIds = [];
//
voteRef.on('child_changed',data=>{
  if(data.key() == 'total')return;
  console.log('==============')
  var options = data.val();
  var total = _.pluck(options,'value').reduce(function(a,b) { return a + b });
  voteRef.update({'total':total});
  store.emit("updated");
});

store.setVote = function(key){
  var optRef = voteRef.child('options/'+key+"/value");
  optRef.once('value',function(data){
    optRef.set((+ data.val()) +1);
  })
}

store.fetchVote = function(id){
  return new Promise(function(resolve, reject){
    voteRef.once('value',function(data){
      resolve(data.val())
    },function(err){
      reject(err);
    });
  })
};

store.fetchVotes = function(){
  return new Promise(function(resolve, reject){
    voteRef.once('value',function(data){
      resolve(data.val())
    },function(err){
      reject(err);
    });
  })
};

store.createUser = function(userInfo){
  //console.log(userInfo);
  userRef.push(userInfo);
};

store.createVote = function(voteInfo){
  return new Promise(function(resolve,reject){
    if(voteInfo.title == ''){
      reject({msg:'标题为空'})
    }else{
      var newObj = {};
      newObj.title   = voteInfo.title;
      newObj.options = {};
      var j = 0;
      for(var i=0; i < voteInfo.options.length;i++){
        newObj.options['opt'+j] = {};
        if(voteInfo.options[i]){
          newObj.options['opt'+j].title = voteInfo.options[i];
          newObj.options['opt'+j].value = 0;
          j++;
        }
      }
      if(newObj.options.length == 0){
        reject({msg:'选项为空'})
      }else{
        var newRef = voteRef.push(newObj,function(err){
          if(err == null){
            var newKey = newRef.key();

            voteIds.push(newKey);
            ref.child('vote-ids').set(voteIds.join());
            resolve(newKey);
          }else{
            //console.log(err);
            reject({msg:err});
          }
        })
      }
    }
  });
}




