<template>
  <div class="hd">
    <h1 class="page_title">{{voteInfo.title}}</h1>
  </div>
  <div class="bd">
    <div class="vote-warp">
      <div class="column"  v-for="option in voteInfo.options" track-by="$index">
        <div class="option"><span>{{* $index | toCharCode option.title  }}</div>
        <div class="progress">
          <div class="progress-bd" :style="{ height: option.percentage }"> </div>
        </div>
      </div>
    </div>
    <cells-title align="center">感谢您的投票</cells-title>
  </div>
</template>
<script>
  import _ from 'underscore'
  import store from '../store'
  import {Cells,Cell,CellsTitle} from 'vue-weui';
  export default{
    name:"Result",
    components:{Cells,Cell,CellsTitle},
    data(){
      return {
        voteInfo:{}
      }
    },
    route:{
      data:function(){
        document.title = "投票结果";
        return store.fetchVote().then(function(vote){
          var voteOptions = _.values(vote.options);
          var maxValue    = _.max(voteOptions,function(option){return option.value}).value;
          _.mapObject(voteOptions,function(val,key){
            return voteOptions[key]['percentage'] = Math.round(val.value/maxValue*100)+'%';
          });
          return {
            voteInfo:vote
          }
        })
      }
    }
  }
</script>
<style>
  .vote-warp{
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -webkit-justify-content: space-between;
    -ms-flex-pack: justify;
    justify-content: space-between;
    height: 300px;
    /*position: absolute;*/
    /*width: 100%;*/
    /*top:80px;*/
    /*bottom: 40px;*/
    background: #f0f0f0;

  }
  .column{
    flex-grow:1;
    background: #666;
    position: relative;
    font-size:12px;
    -webkit-transition: all 1s;
    -moz-transition: all 1s;
    -ms-transition: all 1s;
    -o-transition: all 1s;
    transition: all 1s;
  }
  .option{
    box-sizing: border-box;
    height:40px;
    line-height: 40px;
    position: absolute;
    bottom: 0;
    width: 100%;
    padding:0 5px;
    text-align: center;
    color: #fff;
  }
  .progress{
    box-sizing: border-box;
    position: absolute;
    top: 0;
    bottom: 40px;
    width: 100%;
    background: #ccc;
  }
  .progress-bd{
    margin: 0 auto;
    width: 80%;
    background: #333;
    position: absolute;
    height: 0;
    bottom: 0;
    right: 0;
    left: 0;
    -webkit-transition: all .3s ease;
    -moz-transition: all .3s ease;
    -ms-transition: all .3s ease;
    -o-transition: all .3s ease;
    transition: all .3s ease;
  }
</style>


